<template>
    <body>
        <el-form class="register-container" ref="registerForm" :model="registerForm" label-position="left" label-width="0px">

            <h3 class="register-title">玩家注册</h3>

            <el-form-item>
                <el-input v-model="registerForm.name" type="text" placeholder="用户名" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item>
                <el-input v-model="registerForm.password" type="password" placeholder="密码" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item>
                <el-input v-model="confirmPassword" @input="confirmPasswordFunc" type="password" placeholder="确认密码" autocomplete="off"></el-input>
                <p style="color: red; text-align: center;">{{ confirmWarn }}</p>
            </el-form-item>

            <el-form-item style="text-align: center;width: 100%;">
                <el-button type="primary" style="width:100%;background-color: #505458;border: none;" v-on:click="Register()">确认注册</el-button>
            </el-form-item>

            <el-form-item style="text-align: center;width: 100%;">
                <el-button type="primary" style="width:100%;background-color: #505458;border: none;" v-on:click="Cancel()">取消</el-button>
            </el-form-item>

        </el-form>
    </body>
</template>

<script>
    import axios from 'axios';

    export default {
        name: 'RegisterView',
        data() {
        return {
            confirmPassword: '',
            confirmWarn: '',
            registerForm: {
            name: '',
            password: '',
            },
        };
        },

        methods: {
            async Register() {
                try {
                    if (
                    this.registerForm.name &&
                    this.registerForm.password
                    ){
                        //调用函数将数据发送到后端
                        await this.sendDataToBackend();

                        this.$message({
                            message: '注册成功，欢迎来到用户管理系统',
                            type: 'success'
                        });

                        this.$router.push({
                            path:'/Home',
                            query: {
                            loginName: this.registerForm.name,
                            }
                        });
                    } else {
                        this.$message.error('请填写完整的注册信息');
                    }
                } catch (error) {
                    if (error.response) {
                        // 如果是HTTP响应错误
                        if (error.response.status === 409) {
                            // HTTP状态码为409表示冲突（用户名已存在）
                            this.$message.error('该用户名已存在！');
                        } else {
                            this.$message.error('注册失败，请重新注册或联系管理员');
                        }
                        console.error(error);
                    } else {
                        // 如果不是HTTP响应错误，处理其他错误
                        this.$message.error('注册失败，请重新注册或联系管理员');
                        console.error(error);
                    }
                }
            },

            confirmPasswordFunc() {
                if(this.registerForm.password === this.confirmPassword) {
                    this.confirmWarn = '';
                    return;
                } else {
                    this.confirmWarn = '密码不一致，请从新填写！';
                    return;
                }
            },

            async sendDataToBackend() {
                try {
                    const response = await axios.post('http://localhost:8080/uploadData',this.registerForm,{
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    });

                    //处理后端响应
                    console.log(response.data);
                } catch (error) {
                    //处理错误
                    console.error(error);

                    //将错误抛出
                    throw error;
                }
            },

            Cancel() {
                this.$router.push({
                    path:'/',
                });
            },
        }
    }
</script>

<style>
    .register-container {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 90px auto;
        width:350px;
        padding: 35px 35px 15px 35px;
        background: #fff;
        border:1px solid #eaeaee;
        box-shadow: 0 0 25px #cac6c6;
    }

    .register-title {
        margin: 0px auto 40px auto;
        text-align: center;
    }

    body {
        margin: 0px;
        padding: 0px;
        background-position: center;
        height: 100%;
        width: 100%;
        background-size: cover;
        position: fixed;
    }
</style>


